body {
  overscroll-behavior: none;
  overflow: hidden;
  padding: 0;
  height: 100%;
  max-height: 100%;
}

a {
  color: inherit;
  text-decoration: inherit;
}


/** vyzn overwrites start **/
#root > div > div:nth-child(2) > div:nth-child(4) > div > div:nth-child(3) > div:nth-child(1) > button {
  display: none !important;
}

#root > div > div:nth-child(2) > div > div > form > div:nth-child(1) > div > button {
  display: none !important;
}

#root > div > div:nth-child(2) > div:nth-child(4) > div > div:nth-child(5) > div:nth-child(2) > div > button {
  display: none !important;
}

#root > div > div:nth-child(2) > div:nth-child(3) > button {
  display: none !important;
}
/** vyzn overwrites end **/